const initialState = {
    isAuthenticated: false,
    user: null,
    admin: null,
    userToken: null,
    adminToken: null,
    isAdmin: false
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'LOGIN_USER':
            return {
                ...state,
                isAuthenticated: true,
                user: action.payload,
                userToken: localStorage.setItem('user_Token', action.token)
            };
        case 'LOGOUT_USER':
            return {
                ...state,
                isAuthenticated: false,
                user: null,
                userToken: null,
            };
        case 'LOGIN_ADMIN':
            return {
                ...state,
                isAuthenticated: true,
                admin: action.payload,
                adminToken: localStorage.setItem('admin_Token', action.token)
            };
        case 'LOGOUT_ADMIN':
            return {
                ...state,
                isAuthenticated: false,
                admin: null,
                adminToken: null,
            };
        case 'SET_IS_ADMIN':
            return {
                ...state,
                isAdmin: action.isAdmin,
            };
        default:
            return state;
    }
};

export default authReducer;
